import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { Routes, RouterModule } from '@angular/router';

import { IonicModule } from '@ionic/angular';

import { AreasPage } from './areas.page';
import { MaterialModule } from 'src/app/material.module';

const routes: Routes = [
  {
    path: 'areas',
    component: AreasPage,
    children:[
      { path: 'fabrica', loadChildren: './fabrica/fabrica.module#FabricaPageModule' },
      { path: 'linha', loadChildren: './linha/linha.module#LinhaPageModule' },
      { path: 'secao', loadChildren: './secao/secao.module#SecaoPageModule' },
    ]
  },
  {
    path:'',
    redirectTo:'fabrica',
    pathMatch:'full'
  }
];

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    IonicModule,
    MaterialModule,
    RouterModule.forChild(routes)
  ],
  declarations: [AreasPage]
})
export class AreasPageModule {}
